* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.PreReleaseContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  min-width: 100vw;
  background-color: black;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;

}

h1.HomeTopTitle {
  font-family: 'Roboto', sans-serif;
  font-size: clamp(1.5rem, 5vw, 3rem);
  font-weight: bold;
  text-align: center;
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  background: linear-gradient(90deg, #ff0000, #0084ff, white, red);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  text-shadow: 3px 3px 6px rgba(0, 0, 0, 0.5), 0 0 25px rgba(0, 0, 0, 0.2);
  width: 90%;
  max-width: 1200px;
}
.Whitelist-Container {
}

img.AI1 {
  height: 400px;
  max-width: 100%;
  object-fit: cover;
}

p {
  color: #dbdada;
  text-align: center;
  font-size: clamp(1rem, 2.5vw, 1.5rem);
  font-weight: bold;
  line-height: 1.6;
  padding: 10px;
  width: 90%;
  max-width: 770px;
  margin: 20px auto;
  border-bottom: 2px solid #ddd;
  font-style: oblique;
}

@media (max-width: 768px) {
  .PreReleaseContainer {
    padding: 20px;
  }

  h1.HomeTopTitle {
    font-size: clamp(1.2rem, 4vw, 2.5rem);
    top: 10px;
  }

  img.AI1 {
    height: auto;
    width: 90%;
  }

  p {
    font-size: clamp(0.9rem, 3vw, 1.2rem);
    width: 100%;
  }
}

.LearnReflect {
  margin-top: 10vh;
}

h3 {
  color: white;
  z-index: 2;
  font-size: clamp(1.5rem, 5vw, 3em); /* Responsive font size */
  top: 13vh;
  position: relative;
  display: flex;
  text-align: center; /* Center the heading */
  margin-bottom: 20px; /* Add space below the h3 */
}

h2 {
  color: white;
  z-index: 2;
  position: relative;
  top: 5vh;
  font-size: 3em;
  font-style: italic;
  color: grey;
  display: flex;
}

.social-icon {
  margin: 10px;
  max-width: 100%;
}

.EmailForm {
  display: flex;
  background-color: black;
  width: 90%; /* Use percentage for responsive width */
  max-width: 300px; /* Set a max width for larger screens */
  height: auto; /* Adjust height to auto for responsiveness */
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  top: 185px;
  text-align: center;
  margin: 20px auto; /* Center the form in the container */
  margin-top: 20px; /* Add top margin for spacing from h3 */
}

input {
  width: 100%; /* Full width of the form */
  padding: 10px; /* Add padding */
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: lightgrey;
  color: black; /* Change text color for better contrast */
}

.submitbutton {
  width: 100%; /* Full width of the form */
  height: 35px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.submitbutton:hover {
  background-color: #0056b3; /* Darken the button on hover */
}

label {
  color: black; /* Change label color to black for contrast */
  margin-bottom: 10px; /* Add space below the label */
  text-align: center; /* Center the label text */
}

.img-container {
  display: flex;
  justify-content: center;
  margin-top: 20vh;
  width: auto;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.social-icon-X {
  height: 75px;
}

.social-icon-youtube {
  height: 70px;
}

.EmailConfirmation {
  background-color: yellow;
  width: 500px;
  height: 500px;
  z-index: 2;
  justify-content: center;
  position: relative;
}


/* Modal background */
.modal {
  display: flex;
  position: fixed;
  z-index: 999;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  justify-content: center;
  align-items: center;
}

/* Modal content box */
.modal-content {
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
  width: 400px;
  height: 275px;
  max-width: 80%;
  box-shadow: 0 0 20px 5px rgba(225, 241, 0, 0.8),  /* Rødt lys */
  0 0 40px 10px rgba(0, 255, 128, 0.8), /* Gult lys */
  0 0 60px 15px rgba(0, 0, 255, 0.8); /* Blått lys */
}

/* Close button */
.close {
  position: absolute;
  top: 10px;
  right: 20px;
  font-size: 30px;
  cursor: pointer;
  color: #333;
}


